* {
    box-sizing: border-box;
}

.noSelect:focus {
    outline: none !important;
}
h1, p {
    margin: 0;
    padding-top: 30px;
}

::selection {
	background-color: transparent;
}

.noSelect {
    -webkit-tap-highlight-color: transparent;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
.noSelect:focus {
    outline: none !important;
}

body {
    margin:  0;
    padding: 0;
    background-color: black;
    color: rgba(0,0,0,0.4);
    height: 100vh;
}

#root, .App {
    height: 100%;
}

main {
    background-image: url(../imgs/background.jpg);
    height: 100%;
}

@keyframes game-over {
    0% {
        background-color: rgba(244, 86, 86, 0.285);
        width: 200px;
        height: 200px;
    }
    
    25% {
        background-color: rgba(255, 255, 0, 0.242);
        width: 220px;
        height: 220px;
        transform: translatey(-10px);
    }
    
    50% {
        background-color: rgba(0, 0, 255, 0.152);
        width: 200px;
        height: 200px;
        transform: translatey(0px);
    }
    
    75% {
        background-color: rgba(0, 255, 42, 0.152);
        width: 220px;
        height: 220px;
        transform: translatey(-10px);
    }
    
    100% {
        background-color: rgba(255, 0, 179, 0.152);
        width: 200px;
        height: 200px;
        transform: translatey(0px);
    }
}

@keyframes start-over {
    0% {
        background-color: rgba(244, 86, 86, 0.285);
    }
    
    25% {
        background-color: rgba(255, 255, 0, 0.242);
    }
    
    50% {
        background-color: rgba(0, 0, 255, 0.152);
    }

    75% {
        background-color: rgba(0, 255, 42, 0.152);
    }

    100% {
        background-color: rgba(255, 0, 179, 0.152);
    }
}


.start-over {
    border: unset;
    width: 200px;
    height: 70px;
    border-radius: 2vw;
    animation-name: start-over;
    animation-timing-function: linear;
    animation-duration: 3s;
    animation-iteration-count: infinite;
    color: rgba(255,255,255,0.5);
    cursor: pointer;
    font-size: 24px;
    font-weight: 600;
}

.game-btn-container {
    height: 250px;
}

.mode-btn-container button {
    width: 60px;
    height: 70px;
    animation-name: start-over;
    animation-timing-function: linear;
    animation-duration: 3s;
    animation-iteration-count: infinite;
    background-color: aliceblue;
    margin: 0 5px;
    font-size: 20px;
    border: 1px solid  rgba(255,255,255,0.5);
    font-size: 24px;
    font-weight: 600;
    color: rgba(255,255,255,0.5);
    cursor: pointer;
}

.mode-btn-container button:first-child {
    border-top-left-radius: 2vw;
    border-bottom-left-radius: 2vw;
}

.mode-btn-container button:last-child {
    border-top-right-radius: 2vw;
    border-bottom-right-radius: 2vw;
}

.game-btn {
    box-sizing: border-box;
    width: 200px;
    height: 200px;
    border-radius: 50%;
    border: 1px solid rgb(128, 128, 128);
    margin-top: 30px;
    cursor: pointer;
    font-size: 40px;
    -webkit-tap-highlight-color: rgba(255, 255, 255, 0);
}

.game-over {
    pointer-events: none;
    animation-name: game-over;
    animation-duration: 3s;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
}

.hide{
    visibility: hidden;
}